.home__container {
    width: 90%;
    max-width: 1600px;
    margin: 0 auto;
    background-color: whitesmoke;
}

.home__title {
    font-size: 4rem;
    font-weight: 700;
    padding: 1rem;
    margin-bottom: 1rem;
}

.home__list {
    width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
}

@media(max-width:1100px) {
    .home__list {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media(max-width:780px) {
    .home__list {
        grid-template-columns: repeat(2, 1fr);
    }

    .home__title {
        font-size: 3rem;
    }
}

@media(max-width:550px) {
    .home__list {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}