.timer {
    width: 90%;
    background-color: whitesmoke;
    height: 1.5rem;
    align-self: center;
    border-radius: 1rem;
    position: relative;
}

.timer__inner {
    width: 100%;
    height: 100%;
    background-color: greenyellow;
    border-radius: 1rem;
    position: absolute;
}

.timerAnimation10 {
    animation: timerAnim 10s linear;
}

.timerAnimation15 {
    animation: timerAnim 15s linear;
}

.timerAnimation20 {
    animation: timerAnim 20s linear;
}



/* .stop {}

.resetAnim {} */

@keyframes timerAnim {
    0% {
        width: 100%;
        background-color: greenyellow;
    }

    50% {
        width: 50%;
        background-color: yellow;
    }

    75% {
        background-color: red;
    }

    100% {
        width: 0%;
        background-color: red;
    }
}