.quiz__container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90%;
    max-width: 1600px;
    margin: 0 auto;
    height: 100vh;
    background-color: whitesmoke;
}

.ready__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.ready__title {
    font-size: 3rem;
    font-weight: 700;
    padding-bottom: 2rem;
}

.ready__btn {
    padding: 1rem 2rem;
    border-radius: .5rem;
    border-width: 0;
    background-color: black;
    color: white;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 1.5rem;
    transition: all .3s;
}

.ready__btn:hover {
    background-color: cornflowerblue;
    cursor: pointer;
    color: black;
}

.game__container {
    width: 70%;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    padding: 0 3rem;
}

.game__title {
    font-size: 3rem;
    text-transform: capitalize;
}

.game__question {
    font-size: 2rem;
    font-weight: 700;
}

.alternative__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.question__alternative {
    background-color: black;
    color: white;
    padding: .5rem;
    font-size: 1.5rem;
    font-weight: 500;
    transition: all .3s;
    border-radius: 1rem;
    padding: 1rem;
}

.question__alternative:hover {
    background-color: cornflowerblue;
    color: black;
    cursor: pointer;
}

.game__answer {
    font-size: 2rem;
    font-weight: 700;
}

.endgame__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.endgame__title {
    font-size: 2rem;
    font-weight: 700;
}

.endgame__score {
    font-size: 2rem;
    font-weight: 700;
}

.endgame__btn-container {
    display: flex;
    gap: 2rem;
    justify-content: center;
    align-items: center;
}

.endgame__btn {
    border: none;
    text-decoration: none;
    color: white;
    font-size: 1rem;
    padding: 1rem 1.5rem;
    background-color: black;
    border-radius: 2rem;
    font-weight: 700;
    text-transform: uppercase;
    transition: all .3s;
}

.endgame__btn:hover {
    color: black;
    background-color: cornflowerblue;
}

.correct {
    background-color: greenyellow;
    color: black;
}

.wrong {
    background-color: red;
    color: white;
}

@media(max-width:1100px) {}

@media(max-width:780px) {
    .game__container {
        width: 90%;
    }
}


@media(max-width:550px) {
    .game__container {
        width: 100%;
    }
}