.accordion {
    border-radius: .5rem;
    padding: .75rem;
    cursor: pointer;
    box-shadow: .25rem .25rem .25rem rgba(0, 0, 0, 0.1);
    margin: 1rem 0;
    background-color: black;
    width: 90%;
}

.accordion__header {
    width: 100%;
    border: none;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    font-size: 1.25rem;
    color: white;
}

.accordion__header span {
    max-width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.accordion__body {
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition: all .3s;
    color: white;
}

.accordion__body.active {
    height: 100%;
    opacity: 1;
    padding: .5rem 0;
}

.arrow {
    scale: 1.5;
    width: 2rem;
    transition: transform .2s linear;
}

.accordion:has(.active) .arrow {
    transform: rotate(180deg);
}